import React from "react";
import MainCrosel from "../../components/HomeCarosel/MainCrosel";
import HomeSectionCarosel from '../../components/HomeSectionCarosel/HomeSectionCarosel'
import {mens_kurta} from "../../../Data/mens_kurta";
import {mens_jeans} from "../../../Data/Men/mens_jeans";
import { mens_shirt } from "../../../Data/Men/mens_shirt";
import { womans_dress } from "../../../Data/Women/womans_dress";
import { womens_top } from "../../../Data/Women/womens_top";

const HomePage = () => {
    return (
        <div>
            <MainCrosel></MainCrosel>
            <div className="space-y-10 py-20 flex flex-col justify-center">
                <HomeSectionCarosel data={mens_kurta} sectionName="Men's Kurta" />
                <HomeSectionCarosel data={mens_jeans} sectionName="Men's Jeans" />
                <HomeSectionCarosel data={mens_shirt} sectionName="Men's Shirt" />
                <HomeSectionCarosel data={womens_top} sectionName="Women's Top" />
                <HomeSectionCarosel data={womans_dress} sectionName="Woman's Dress" />
            </div>
        </div>
    )
}

export default HomePage;